body {
  background: #070625;
  color: white;
  font-family: 'Dosis', sans-serif;
}

nav {
  display: flex;
  border-bottom: #393857 solid 1px;
  font-family: 'Oswald', sans-serif;
  font-weight: 100; 
}

h1 {
  font-size: 60px;
  font-family: 'Oswald', sans-serif;
  font-weight: 100;
}

h2 {
  font-family: 'Oswald', sans-serif;
  font-weight: 100; 
  font-size: 16px;
}

.bottomText p {
  font-size: 30px;
}

.planetinfo {
  font-size: 28px;
}
#planetlink {
  font-size: 20px;
}

#textDiv {
  max-width: 50vh;
}

#navDiv {
  text-align: right;
  margin: 2vh;
}

.container {
  max-width: 130vh;
  margin: 0 auto;
  min-width: 40rem;
}

.navButtons {
  margin: 1rem;
  color: white;
  text-decoration: none;
}

#topContainer, #bottomContainer {
  display: flex;
}

.planetImg {
  max-width: 50%;
  min-width: auto;
  padding:  5vh 10vh 10vh 10vh;
  margin: 0 auto;
  display: block;
  width: auto;
  height: 100%;
}

#canvas {
  max-width: 100%;
  min-width: auto;
  padding:  5vh 10vh 10vh 10vh;
  margin: 0 auto;
  display: block;
  width: 60vh;
  height: 60vh;
}

#navDiv {
  margin-left: 120vh;
}

.categoryButton {
  display: block;
  border: #393857 1px solid;
  margin: .7rem 0 0 0;
  padding: .8rem;
  color: white;
  text-decoration: none;
  font-size: 24px;
}

.categoryButtonPressed {
  display: block;
  background: #6D2ED5;
  border: #6D2ED5 1px solid;
  margin: 1rem 0 0 0;
  color: white;
}

.bottomText {
  border: #393857 1px solid;
  padding: 1vh 2vh 0 2vh;
  width: 26vh;
}

#bottomContainer {
  justify-content: space-between;
}

#navName {
  margin: 1.3vh 2vh 2vh 2vh;
}